import { useEffect, useRef, useContext, useReducer, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";
import "../css/Contact.css";
import { UserContext } from "../context/UserContext";
import api from "../api/axiosConfig";
import { chatReducer, initialChatState } from "../context/ChatReducer";
import useApi from "./../hooks/useApi";
import { formatToHHmm } from "../utils/DateTimeUtils";
import LoadingOverlay from "../component/LoadingOverlay";

function Contact() {
  // useContext    .... useReducer  ..
  const [state, dispatch] = useReducer(chatReducer, initialChatState);
  const { user, userRole, authLoading } = useContext(UserContext);

  // userRole   username 
  const isAdmin = userRole === "ADMIN"; // true
  const isUser = userRole === "USER"; // false
  const adminUsername = isAdmin && user;
  const userUsername = isUser && user;

  const { roomId } = useParams(); //  
  const stompClient = useRef(null);
  const listRef = useRef(null); //  
  const endOfMessagesRef = useRef(null); //  

  const { loading, request } = useApi(); //  ?
  const navigate = useNavigate();

  //     +    +    
  const welcomeMessage = {
    sender: adminUsername,
    text: `${user}, MailBuddy   !    `,
    buttons: [
      { id: 1, label: "Gmail  " },
      { id: 2, label: " " },
    ],
    type: "welcome",
  };
  const [guideStep, setGuideStep] = useState(0); // 0:  , 1: Gmail, 2: , 3:    ,   

  useEffect(() => {
    if (authLoading) return;

    if (!user) {
      navigate("/err401", { replace: true });
      return;
    }
  }, [user, authLoading]);

  //  STOMP 
  // STOMP  /topic/chat/     
  useEffect(() => {
    // 1) SockJS   (   )
    const socket = new SockJS("http://ec2-52-79-115-253.ap-northeast-2.compute.amazonaws.com:8888/ws/chat");
    // 2) STOMP  , SockJS  , (5 )   
    const client = new Client({
      webSocketFactory: () => socket,
      reconnectDelay: 5000,
    });

    // 3)      (  )
    client.onConnect = () => {
      dispatch({ type: "CONNECT" });
      // 4)  (=) ,    
      client.subscribe(`/topic/chat/${roomId}`, (msg) => {
        try {
          const content = JSON.parse(msg.body); // msg.body =     JSON .
          const serverMessage = {
            sender: content.sender || "", //  sender    ,  
            role: content.role || "USER", //    userrole   user
            text: content.content || content.text, //      
            // ts: new Date().toISOString(), //   
            ts: content.createdAt, //    ***   ..?????
            isRead: content.read ?? content.isRead ?? false, //  
          };
          dispatch({ type: "ADD_MESSAGE", payload: serverMessage });
          //      chat  
          setTimeout(() => {
            listRef.current?.scrollTo({
              top: listRef.current.scrollHeight,
              behavior: "smooth",
            });
          }, 100); //       
        } catch {
          // msg.body JSON    -      .
          dispatch({
            type: "ADD_MESSAGE",
            payload: {
              sender: "",
              text: msg.body,
              ts: new Date().toISOString(),
            },
          });
        }
      });
    };

    // 5) STOMP     ()
    client.onStompError = (frame) => {
      console.error(" STOMP :", frame);
      dispatch({ type: "SET_ERROR", payload: "   " });
    };

    // 6) STOMP   (  )
    client.activate();
    // 7)     
    stompClient.current = client;
    return () => client.deactivate();
  }, [roomId]);
  //  STOMP  

  //     
  //            
  useEffect(() => {
    (async () => {
      if (!user) return;
      if (isAdmin) return;
      // ADMIN     USER  .
      // ADMIN      ,       .
      try {
        const res = await api.post("/api/chat/hasroom", {
          withCredentials: true,
        });
        if (res.data.roomId) {
          navigate(`/contact/${res.data.roomId}`);
        }
      } catch (err) {
        console.error("  :", err);
      }
    })();
  }, [user]);

  //     ****************************
  const loadMessages = async () => {
    if (!roomId || !user) return;
    try {
      const res = await request(() => api.get(`/api/chat/${roomId}`));
      const chatMessages = res.map((c) => ({
        sender: c.username,
        role: c.userRole,
        text: c.content,
        ts: c.createdAt,
        isRead: c.isRead,
      }));
      //      guideStep 3  input  
      if (chatMessages.length > 0) {
        setGuideStep(3);
      }
      //      . some() :     true 
      //    !
      if (userRole !== "ADMIN") {
        const hasWelcome = chatMessages.some((msg) => msg.type === "welcome");
        const listMessages = hasWelcome
          ? chatMessages
          : [welcomeMessage, ...chatMessages]; //    
        dispatch({
          type: "SET_MESSAGES",
          payload: listMessages,
        });
      } else {
        //         
        const listMessages = chatMessages;
        dispatch({
          type: "SET_MESSAGES",
          payload: listMessages,
        });
      }
    } catch (err) {
      console.error("   :", err);
    }
  };
  useEffect(() => {
    loadMessages();
  }, [roomId, user, userRole]);

  //    (   send) - db    -   
  const handleMenuClick = (id) => {
    let reply = "";
    if (id === 1) {
      reply =
        "Gmail       .\n[admin, admin@gmail.com]";
      setGuideStep(1);
    } else if (id === 2) {
      reply = " ?   .";
      setGuideStep(2);
    }
    dispatch({
      type: "ADD_MESSAGE",
      payload: {
        sender: adminUsername,
        text: reply,
        type: "response",
        ts: new Date().toISOString(),
      },
    });
  };

  //    +  (Publish) +    
  const send = () => {
    if (!state.text.trim() || !stompClient.current?.connected) return;
    const body = JSON.stringify({ content: state.text.trim() });
    stompClient.current.publish({
      destination: `/app/chat/${roomId}/send`,
      body,
    });
    dispatch({ type: "CLEAR_TEXT" });
  };

  //   ADD   !
  useEffect(() => {
    // messages     !
    if (state.messages.length === 0) return;

    const lastMsg = state.messages[state.messages.length - 1];
    //   (user)   
    if (
      guideStep === 1 &&
      lastMsg.role === "USER" &&
      lastMsg.sender === userUsername
    ) {
      if (lastMsg.text.includes("@") && lastMsg.text.length > 6) {
        //    
        dispatch({
          type: "ADD_MESSAGE",
          payload: {
            sender: adminUsername,
            text: "  ! 24   .",
            type: "response",
            ts: new Date().toISOString(),
          },
        });
        setGuideStep(3);
      } else {
        dispatch({
          type: "ADD_MESSAGE",
          payload: {
            sender: adminUsername,
            text: "  .    .",
            type: "response",
            ts: new Date().toISOString(),
          },
        });
      }
    }
    if (
      guideStep === 2 &&
      lastMsg.role === "USER" &&
      lastMsg.sender === userUsername
    ) {
      dispatch({
        type: "ADD_MESSAGE",
        payload: {
          sender: adminUsername,
          text: " !  .",
          type: "response",
          ts: new Date().toISOString(),
        },
      });
      setGuideStep(3); //  !
    }
  }, [state.messages]);

  //        
  useEffect(() => {
    if (endOfMessagesRef.current) {
      //    auto,  smooth 
      endOfMessagesRef.current.scrollIntoView({
        behavior: state.messages.length > 0 ? "smooth" : "auto",
      });
    }
  }, [state.messages]);

  //       
  const onKeyDown = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      send();
    }
  };

  if (loading) return <div className="loading"> </div>;
  // if (!user) {return <div className="loading">  </div>};

  return (
    <div className="chat-wrap">
      <div className="chat-card">
        <div className="chat-header">
          <div className="chat-title"></div>
        </div>

        {user && !state.connected && (
          <LoadingOverlay text=" ..." />
        )}
        {state.error && (
          <div
            style={{
              margin: "8px 0",
              color: "#b91c1c",
              border: "1px solid #f7cbcb",
              borderRadius: 10,
              padding: "8px 10px",
              fontWeight: 700,
            }}
          >
            {state.error}
          </div>
        )}

        {/*   */}
        <div className="chat-list" ref={listRef}>
          {state.messages.map((m, i) => {
            // const isMe = m.sender === user; //   *****************************
            const isMe =
              (isAdmin && m.sender === "admin") ||
              (userRole !== "ADMIN" && m.sender === user);

            const cls = ["chat-item"];
            if (isMe) cls.push("isMe");

            const hideTime = m.buttons && m.buttons.length > 0;

            return (
              <div key={i} className={cls.join(" ")}>
                <div className="bubble">
                  {/*    */}
                  <div className="sender">{m.sender}</div>
                  <div className="text">{m.text}</div>

                  {/*   */}
                  {m.buttons && m.buttons.length > 0 && (
                    <div className="bubble-btns">
                      {m.buttons.map((btn) => (
                        <button
                          key={btn.id}
                          onClick={() => handleMenuClick(btn.id)}
                          className="bubble-btn"
                        >
                          {btn.label}
                        </button>
                      ))}
                    </div>
                  )}
                  {!hideTime && (
                    <div className="meta-row">
                      <div className="time">{formatToHHmm(m.ts)}</div>
                      {/*   :     
                    {isMe && (
                      <div className="read-state">
                        {m.isRead ? "" : ""}
                      </div>
                    )} */}
                    </div>
                  )}
                  <div ref={endOfMessagesRef}></div>
                </div>
              </div>
            );
          })}
        </div>

        {/* :     */}
        {guideStep !== 0 || isAdmin ? (
          <div className="chat-input-row">
            <textarea
              className="chat-input"
              rows={2}
              placeholder={
                state.connected ? " " : "  "
              }
              value={state.text}
              onChange={(e) => {
                const text = e.target.value;

                if (text.length > 300) {
                  dispatch({
                    type: "SET_ERROR",
                    payload: " 300   .",
                  });
                } else {
                  dispatch({ type: "SET_ERROR", payload: null });
                }
                dispatch({ type: "SET_TEXT", payload: text });
              }}
              onKeyDown={onKeyDown}
              disabled={!state.connected}
            />
            <button
              className="chat-send"
              onClick={send}
              disabled={
                !state.connected ||
                !state.text?.trim() ||
                state.text?.length > 300 // 300    
              }
            >
              
            </button>
          </div>
        ) : null}
      </div>
    </div>
  );
}

export default Contact;
