import { Route, Routes, useNavigate } from "react-router-dom";
import "./App.css";
import Navbar from "./component/Navbar";
import Home from "./pages/Home";
import Signup from "./pages/Signup";
import Login from "./pages/Login";
import api from "./api/axiosConfig";
import { useEffect, useState } from "react";
import { UserContext } from "./context/UserContext";
import AddressBook from "./pages/AddressBook";
import Schedule from "./pages/Schedule";
import MyPage from "./pages/MyPage";
import { BirthContext } from "./context/BirthContext";
import ErrorPage from "./component/ErrorPage";
import Contact from "./pages/Contact";
import ContactAdmin from "./pages/ContactAdmin";
import { RefreshProvider } from "./context/RefreshContext";
import Footer from "./component/Footer";
import Faq from "./pages/Faq";
import Guide from "./pages/Guide";
import Terms from "./pages/Terms";

function App() {
  const [user, setUser] = useState(null); //    (username)
  const [birth, setBirth] = useState(null);
  const [googleUser, setGoogleUser] = useState(null); //    (username)
  const [userRole, setUserRole] = useState(null); //     (ROLE_USER, ROLE_ADMIN)
  const [authLoading, setAuthLoading] = useState(true);

  const navigate = useNavigate();

  const checkUser = async () => {
    try {
      const res = await api.get("/api/auth/me");
      //        
      if (
        res.data &&
        res.data.username &&
        res.data.username !== "anonymousUser"
      ) {
        setUser(res.data.username);
        setBirth(res.data.birth);
        setUserRole(res.data.userRole);
      } else {
        setUser(null);
        setBirth(null);
      }
    } catch (err) {
      setUser(null);
      setBirth(null);
    }
  };

  //       
  const checkGoogleUser = async () => {
    try {
      const res = await api.get("/api/auth/me2");
      if (res.data && res.data.email) {
        setGoogleUser(res.data.email);
      } else {
        setGoogleUser(null);
      }
    } catch (err) {
      setGoogleUser(null);
    }
  };

  useEffect(() => {
    Promise.all([checkUser(), checkGoogleUser()]).finally(() =>
      setAuthLoading(false)
    );
  }, []);

  const handleLogout = async () => {
    try {
      if (!window.confirm("  ?")) {
        return;
      } else {
        //   API  (, OAuth2  )
        api.post("/api/auth/logout");

        setUser(null);
        setGoogleUser(null);

        //    ()     +   
        const googleLogoutPopup = window.open(
          "https://accounts.google.com/Logout",
          "GoogleLogout",
          "width=500,height=600"
        );
        setTimeout(() => {
          if (googleLogoutPopup && !googleLogoutPopup.closed) {
            googleLogoutPopup.close();
          }
        }, 100); // 1000 = 1
      }
      // alert("  .");
      navigate("/", { replace: true });
    } catch (error) {
      console.error("   :", error);
    }
  };

  return (
    <RefreshProvider>
      <UserContext.Provider value={{ user, googleUser, userRole, authLoading }}>
        <BirthContext.Provider value={birth}>
          <div className="App">
            <Navbar onLogout={handleLogout} checkGoogleUser={checkGoogleUser} />
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/login" element={<Login checkUser={checkUser} />} />
              <Route path="/signup" element={<Signup />} />
              <Route path="/mypage" element={<MyPage setBirth={setBirth} />} />
              <Route path="/address" element={<AddressBook />} />
              <Route path="/schedule" element={<Schedule />} />
              <Route path="/contact/:roomId" element={<Contact />} />
              <Route path="/contact_admin" element={<ContactAdmin />} />
              {/*        */}
              <Route path="*" element={<ErrorPage />} />
              {/* 401   */}
              <Route path="/err401" element={<ErrorPage statusCode={401} />} />
              {/*   ? */}
              <Route path="/faq" element={<Faq />} />
              <Route path="/guide" element={<Guide />} />
              <Route path="/terms" element={<Terms />} />
            </Routes>
            <Footer />
          </div>
        </BirthContext.Provider>
      </UserContext.Provider>
    </RefreshProvider>
  );
}

export default App;
