import React from "react";
import "../css/ErrorPage.css";
import { useNavigate, useSearchParams } from "react-router-dom";
//useSearchParams -> URL(?status=400&message=...)    

//  : /error?status=400&message=++(...)+++
const ErrorPage = ({
  statusCode: propStatusCode,
  message: propMessage,
} = {}) => {
  // props      -> ={} :    -> props   
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  // URL   
  const queryStatus = searchParams.get("status");
  const queryMessage = searchParams.get("message");
  //   -> response.sendRedirect("http://localhost:3000/error?status=400&message=" + encodedMsg);
  // queryString ->  400 ,queryMessage  "  (...)   .

  //  statusCode / message 
  const finalStatusCode =
    propStatusCode ?? (queryStatus ? Number(queryStatus) : undefined) ?? 404;

  const finalMessage = propMessage ?? queryMessage;

  const defaultMessage = {
    401: "     ,  .",
    404: "    .",
    500: "  .  .",
    default: "    .",
  };
  return (
    <div style={{ textAlign: "center", padding: "50px" }}>
      <h1>Error {finalStatusCode}</h1>
      <p>
        {finalMessage ||
          defaultMessage[finalStatusCode] ||
          defaultMessage.default}
      </p>
      <button className="error-btn" onClick={() => navigate("/")}>
         
      </button>
      {/*400     */}
      {String(finalStatusCode) === "400" && (
        <button
          className="login-btn"
          onClick={() =>
            (window.location.href =
              "http://ec2-52-79-115-253.ap-northeast-2.compute.amazonaws.com:8888/oauth2/authorization/google")
          }
        >
           Google   
        </button>
      )}
    </div>
  );
};
export default ErrorPage;
