import { Link } from "react-router-dom";
import "../css/Navbar.css";
import { useContext } from "react";
import { UserContext } from "../context/UserContext";

function Navbar({ onLogout, checkGoogleUser }) {
  const { user, googleUser, userRole } = useContext(UserContext);

  //    
  const handleGoogleLogin = () => {
    //   OAuth2   
    window.location.href = "/oauth2/authorization/google";
    //     url    ( )
    checkGoogleUser();
  };

  return (
    <header className="navbar">
      <div className="nav-left">
        <Link to="/" className="brand">
          MailBuddy<span className="brand-emoji"></span>
        </Link>
      </div>

      <nav className="nav-center">
        <Link to="/"></Link>
        <Link to="/schedule"> </Link>
        {user && googleUser && <Link to="/address"></Link>}
        {!user && <Link to="/faq">FAQ</Link>}
        <Link to="/guide"> </Link>
        {user && userRole === "USER" && (
          <Link to="/contact/:roomId"></Link>
        )}
        {userRole === "ADMIN" && (
          <Link to="/contact_admin">  </Link>
        )}
        {user && <Link to="/mypage"></Link>}
      </nav>

      <div className="nav-right">
        {!user ? (
          <>
            <Link to="/login" className="btn ghost">
              
            </Link>
            <Link to="/signup" className="btn solid">
              
            </Link>
          </>
        ) : (
          <>
            {!googleUser && (
              <button onClick={handleGoogleLogin} className="btn google">
                 Google 
              </button>
            )}
            <button className="btn solid" onClick={onLogout}>
              
            </button>
          </>
        )}
      </div>
    </header>
  );
}

export default Navbar;
