import "../css/AddressBook.css";
import React, { useContext, useEffect, useRef, useState } from "react";
import api from "../api/axiosConfig";
import { UserContext } from "../context/UserContext";
import useApi from "../hooks/useApi";
import ErrorPage from "../component/ErrorPage";
import { useNavigate } from "react-router-dom";

function AddressBook() {
  const { user, googleUser, authLoading } = useContext(UserContext);
  const { error, loading, request, setError } = useApi();
  const [addresses, setAddresses] = useState([]);
  const navigate = useNavigate();

  // :  
  const [search, setSearch] = useState("");

  //  (      )
  const [currentPage, setCurrentpage] = useState(0);
  const itemsPerPage = 7; //    

  //  :   
  const filteredAddresses = addresses.filter((a) => {
    const q = search.trim().toLowerCase();
    if (!q) return true; //   
    const name = (a.senderName || "").toLowerCase();
    const email = (a.senderEmail || "").toLowerCase();
    return name.includes(q) || email.includes(q);
  });

  //    
  const totalPages = Math.ceil(filteredAddresses.length / itemsPerPage);
  const startIndex = currentPage * itemsPerPage;
  //      
  const currentData = filteredAddresses.slice(
    startIndex,
    startIndex + itemsPerPage
  );

  //   1 
  useEffect(() => {
    setCurrentpage(0);
  }, [search]);

  // Gmail   (to  , subject , body )
  const openGmailCompose = (to, subject = "", body = "") => {
    const qs = new URLSearchParams({
      view: "cm", // compose mode,     
      fs: "1", // full screen 
      to: to || "",
      su: subject,
      body: body,
    }).toString();
    window.open(
      `https://mail.google.com/mail/?${qs}`,
      "_blank",
      "noopener,noreferrer"
    );
  };

  //  
  const getAllAddress = async () => {
    try {
      const res = await request(() => api.get("/api/address/list"));
      setAddresses(res ?? []);
    } catch (err) {
      setError(err);
      //setError(error.response?.data?.message || error.message);
      console.error(err);
    }
  };

  useEffect(() => {
    if (authLoading) return;

    if (googleUser) {
      getAllAddress();
    } else {
      navigate("/err401", { replace: true });
      return;
    }
  }, [googleUser, authLoading]);

  if (!googleUser)
    return <div className="ab-error">     .</div>;
  if (loading) return <div className="ab-wrap"> ...</div>;
  if (error && typeof error === "string" && error.includes("")) {
    //     ""   (   ..)
    return <ErrorPage statusCode={500} message={error} />;
  }

  if (loading) return <div className="ab-wrap"> ...</div>;

  if (error && typeof error === "string" && error.includes("")) {
    return <ErrorPage statusCode={500} message={error} />;
  }

  return (
    <div className="ab-wrap">
      <div className="ab-title-row">
        <h2 className="ab-title"></h2>

        {/*  + Gmail   */}
        <div className="ab-actions">
          <input
            type="text"
            className="ab-search-input"
            placeholder="   "
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
      </div>

      {error && <div className="ab-error">{error}</div>}

      <div className="ab-card">
        <div className="ab-head">
          <div className="ab-col ab-name"></div>
          <div className="ab-col ab-email"></div>
          <div className="ab-col ab-action"></div>
        </div>
        <div className="ab-body">
          {addresses.length === 0 && (
            <div className="ab-empty">  .</div>
          )}

          {currentData.map(
            (
              a
              // p // p => 
            ) => (
              <div className="ab-row" key={a.senderEmail}>
                <div className="ab-col ab-name">{a.senderName || "-"}</div>
                <div className="ab-col ab-email">{a.senderEmail}</div>
                <div className="ab-col ab-action">
                  <button
                    className="ab-mail-btn"
                    type="button"
                    onClick={() =>
                      openGmailCompose(
                        a.senderEmail,
                        `[MailBuddy] ${a.senderName ?? ""} `,
                        ""
                      )
                    }
                  >
                    
                  </button>
                </div>
              </div>
            )
          )}
        </div>
      </div>

      {/*   */}
      {totalPages > 1 && (
        <div className="ab-pagination">
          {Array.from({ length: totalPages }, (_, i) => (
            <button
              key={i}
              onClick={() => setCurrentpage(i)}
              className={`ab-page-btn ${i === currentPage ? "active" : ""}`}
              type="button"
            >
              {i + 1}
            </button>
          ))}
        </div>
      )}
    </div>
  );
}

export default AddressBook;
