import React, { useEffect, useRef, useState } from "react";
import api from "../api/axiosConfig";
import "../css/Todo.css";
import TodoRow from "./TodoRow";
import { useScheduleState } from "../context/ScheduleContext";
import { formatToYM } from "../utils/DateTimeUtils";
import useApi from "../hooks/useApi";

// const getMsg = (e, fallback) =>
//   e?.response?.data?.message || e?.message || fallback;

export default function Todo({ userKey }) {
  const [list, setList] = useState([]);
  const [text, setText] = useState("");
  const [authMsg, setAuthMsg] = useState(""); // 401  

  const { error, loading, request, setError } = useApi();

  const inputRef = useRef(null);
  const disabled = !userKey; // userKey   

  const { activeStartDate } = useScheduleState();
  const ymKey = formatToYM(activeStartDate); // yyyy-mm  

  // 401  
  const handleAuthError = (err, fallbackMsg) => {
    if (err?.response?.status === 401) {
      setAuthMsg(err.response.data?.message || fallbackMsg);
      setError(null); //    
      return true; // 401  
    }
    return false;
  };

  // ,,  
  const safeRequest = async (apiCall, authFallbackMsg) => {
    try {
      const data = await request(apiCall); // useApi.request 
      return data;
    } catch (err) {
      // 401  
      if (handleAuthError(err, authFallbackMsg)) {
        return;
      }
      // 401   useApi error   
      throw err;
    }
  };

  //  
  const load = async () => {
    if (!ymKey) return setList([]);

    if (!userKey) {
      setList([]);
      setError(null);
      return;
    }

    try {
      const data = await safeRequest(
        () =>
          api.get("/api/todo", {
            params: { ym: ymKey },
            withCredentials: true,
          }),
        " ."
      );
      if (data) {
        setList(Array.isArray(data) ? data : []);
        setAuthMsg("");
      }
    } catch (err) {
      setList([]);
    }
  };

  useEffect(() => {
    setText("");
    setList([]);
    setError(null);
    setAuthMsg("");

    if (userKey) {
      load();
    }
  }, [ymKey, userKey]);

  //   
  const add = async () => {
    if (disabled) return;
    const t = text.trim();
    if (!t) return;

    try {
      await safeRequest(
        () => api.post("/api/todo", { ym: ymKey, text: t }, { withCredentials: true }),
        " ."
      );
      setText("");
      inputRef.current?.focus();
      await load();
    } catch (err) {
      // 401   error state   
    }
  };

  //   
  const toggleDone = async (id, cur) => {
    if (disabled) return;

    try {
      await safeRequest(
        () => api.patch(`/api/todo/${id}`, { done: !cur }, { withCredentials: true }),
        " ."
      );
      await load();
    } catch (err) {}
  };

  // 
  const edit = async (id, newText) => {
    if (disabled) return;
    const t = (newText ?? "").trim();
    if (!t) return;

    try {
      await safeRequest(
        () => api.patch(`/api/todo/${id}`, { text: t }, { withCredentials: true }),
        " ."
      );
      await load();
    } catch (err) {}
  };

  // 
  const remove = async (id) => {
    if (disabled) return;

    try {
      await safeRequest(() => api.delete(`/api/todo/${id}`, { withCredentials: true }), " .");
      await load();
    } catch (err) {}
  };

  //    
  const clearCompleted = async () => {
    if (disabled) return;

    try {
      await safeRequest(
        () =>
          api.delete("/api/todo/completed", {
            params: { ym: ymKey },
            withCredentials: true,
          }),
        " ."
      );
      await load();
    } catch (err) {}
  };

  const doneCount = list.filter((it) => it.done).length;

  return (
    <div className="todo-card">
      <div className="todo-head">
        <div className="todo-title"> To-Do</div>
        <div className="todo-date">{ymKey}</div>
      </div>

      {authMsg && <div className="todo-banner">{authMsg}</div>}

      <div className="todo-input-block">
        <input
          ref={inputRef}
          className="todo-input"
          placeholder={disabled ? "   " : "   Enter"}
          value={text}
          onChange={(e) => setText(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && add()}
          disabled={disabled || loading} // loading useApi
        />
        <div className="todo-button-row">
          <button className="todo-btn primary" onClick={add} disabled={disabled || loading}>
            {loading ? " ..." : ""}
          </button>
          <button
            className="todo-btn ghost"
            onClick={clearCompleted}
            disabled={disabled || doneCount === 0 || loading}
            title={doneCount ? ` ${doneCount} ` : "  "}
          >
            
          </button>
        </div>
      </div>

      {error && <div className="todo-error">{error}</div>}

      <ul className="todo-list">
        {list.length === 0 && <li className="todo-empty">{loading ? " ..." : "  ."}</li>}
        {list.map((it) => (
          <TodoRow
            key={it.id}
            item={it}
            onEdit={edit}
            onRemove={() => remove(it.id)}
            onToggle={() => toggleDone(it.id, it.done)}
            disabled={disabled || loading}
          />
        ))}
      </ul>
    </div>
  );
}
