import { useContext, useState } from "react";
import api from "../../api/axiosConfig";
import "../../css/CalendarNav.css";
import { UserContext } from "../../context/UserContext";
import { useRefresh } from "../../context/RefreshContext";
import useApi from "../../hooks/useApi";
import { useScheduleDispatch } from "../../context/ScheduleContext";

//   (ai, , )
const CalendarNav = ({ aiLoading, setAiLoading, loadSummaries }) => {
  const [aiError, setAiError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState("");

  const { trigger } = useRefresh();

  const { googleUser } = useContext(UserContext);

  const isErrorMsg = typeof message === "string" && (message.includes("") || message.includes(""));

  // nav     
  const dispatch = useScheduleDispatch();
  const handleGoToday = () => {
    dispatch({ type: "SET_SELECTED_DATE", payload: new Date() });
    dispatch({ type: "SET_ACTIVE_START_DATE", payload: new Date() });
  };

  // AI  
  const handleSummarize = async () => {
    setAiLoading(true); //  
    setAiError(null);
    setMessage("");

    try {
      await api.post("/api/summarize");
      await loadSummaries(); // ,   
      trigger(); // 
    } catch (e) {
      console.error(e);
      setAiError(` : ${e.response?.data?.message || e.message}`);
    } finally {
      setAiLoading(false); //  
    }
  };

  //  10  
  const handleSaveEmails = async () => {
    setIsLoading(true);
    setMessage("");
    try {
      const res = await api.get("/api/gmail/messages/save-top10");
      setMessage(res.data);
    } catch (error) {
      console.error("Error:", error);
      setMessage("  : " + (error.response?.data?.message || error.message));
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="cal-toolbar">
      <button className="btn-today" type="button" onClick={handleGoToday} title="   ">
         
      </button>

      {/* AI   (  ) */}
      {googleUser && (
        <button
          className="btn-ai"
          title="DB  Gmail AI  /"
          onClick={handleSummarize}
          disabled={aiLoading}
        >
          {aiLoading ? " " : "AI "}
        </button>
      )}

      {/* AI   */}
      {aiError && <span className="ai-error">{aiError}</span>}

      {/*  10    (  ) */}
      {googleUser && (
        <button
          onClick={handleSaveEmails}
          disabled={isLoading} //    
          className="btn-mail"
          title="Gmail  10   "
        >
          {isLoading ? " ..." : "  10  "}
        </button>
      )}

      {/* /  */}
      {!!message && <span className={`mini-msg ${isErrorMsg ? "error" : "success"}`}>{message}</span>}
    </div>
  );
};

export default CalendarNav;
