import { useState } from "react";
import api from "../api/axiosConfig";
import { useNavigate } from "react-router-dom";
import "../css/Signup.css";
import useApi from "../hooks/useApi";
import ErrorPage from "../component/ErrorPage";

function Signup() {
  const { error, request, setError } = useApi();
  const navigate = useNavigate();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [birth, setBirth] = useState("");

  //   
  const handleSignup = async (e) => {
    e.preventDefault();
    setError(null);

    try {
      await request(() => api.post("/api/auth/signup", { username, password, name, birth }));
      alert(" !");
      navigate("/login", { replace: true });
    } catch (err) {
      if (err.response?.status === 400) {
        //      (idError, username, name, birth, password)
        setError(err.response.data);
      } else {
        console.error(err);
        setError("  .");
      }
    }
  };

  if (error && typeof error === "string" && error.includes("")) {
    //     ""   (   ..)
    return <ErrorPage statusCode={500} message={error} />;
  }

  return (
    <div className="signup-container">
      <div className="signup-box">
        <h2> </h2>
        <p className="helper">   MailBuddy    .</p>

        {error && typeof error === "object" ? (
          <>
            {error.idError && <p className="field-error">{error.idError}</p>}
            {error.username && <p className="field-error">{error.username}</p>}
            {error.password && <p className="field-error">{error.password}</p>}
            {error.name && <p className="field-error">{error.name}</p>}
            {error.birth && <p className="field-error">{error.birth}</p>}
          </>
        ) : (
          error && <p className="error-message">{error}</p>
        )}

        <form onSubmit={handleSignup} className="signup-form">
          <div className="form-row">
            <input
              type="text"
              placeholder=""
              value={username}
              onChange={(e) => setUsername(e.target.value.trim())}
              autoComplete="username"
              required
            />
          </div>

          <div className="form-row">
            <input
              type="password"
              placeholder=""
              value={password}
              onChange={(e) => setPassword(e.target.value.trim())}
              autoComplete="new-password"
              required
            />
          </div>

          <div className="form-row">
            <input
              type="text"
              placeholder=""
              value={name}
              onChange={(e) => setName(e.target.value.trim())}
              autoComplete="name"
              required
            />
          </div>

          <div className="form-row">
            <input
              type="text"
              placeholder=" (YYYY-MM-DD)"
              value={birth}
              onChange={(e) => setBirth(e.target.value.trim())}
              inputMode="numeric"
              autoComplete="bday"
            />
          </div>

          <div className="actions">
            <button type="submit" className="btn solid">
              
            </button>
            <button type="button" className="btn ghost" onClick={() => navigate("/")}>
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Signup;
