import React, { useEffect, useRef, useState } from "react";

// Todo ->    (, , , )
export default function TodoRow({
  item,
  onEdit,
  onRemove,
  onToggle,
  disabled,
}) {
  const [editing, setEditing] = useState(false);
  const [val, setVal] = useState(item.text);
  const ref = useRef(null);

  // 
  useEffect(() => {
    if (editing) ref.current?.focus(); //input DOM 
  }, [editing]); // editing  

  const commit = () => {
    if (disabled) return setEditing(false); //      
    const trimmed = (val ?? "").trim(); //val null undefined 
    if (trimmed && trimmed !== item.text) onEdit(item.id, trimmed); //  ,   onEdit
    setEditing(false);
  };

  return (
    <li className={`todo-row ${item.done ? "done" : ""}`}>
      <input
        type="checkbox"
        className="todo-check"
        checked={!!item.done} // !! : boolean 
        onChange={onToggle}
        disabled={disabled} //    
        title={disabled ? "   " : "/"} //   -> :
      />

      {editing ? (
        <input
          ref={ref} // input DOM -> ref.current -> useEffect focus() 
          className="todo-edit"
          value={val}
          onChange={(e) => setVal(e.target.value)}
          onBlur={commit} //blur( ) -> commit() -> 
          onKeyDown={(e) => e.key === "Enter" && commit()}
          disabled={disabled}
        />
      ) : (
        <span
          className={`todo-text ${disabled ? "disabled" : ""}`}
          onDoubleClick={() => !disabled && setEditing(true)} // disabled()  editing
          title={disabled ? "   " : " "}
        >
          {item.text}
        </span>
      )}

      <div className="todo-actions">
        {!editing && (
          <button
            className="icon-btn"
            onClick={() => !disabled && setEditing(true)}
            disabled={disabled}
          >
            
          </button>
        )}
        <button className="icon-btn" onClick={onRemove} disabled={disabled}>
          
        </button>
      </div>
    </li>
  );
}
