import React, { useContext, useEffect, useRef, useState } from "react";
import { UserContext } from "../context/UserContext";
import api from "../api/axiosConfig";
import "../css/MyPage.css";
import { useNavigate } from "react-router-dom";
import useApi from "../hooks/useApi";
import ErrorPage from "../component/ErrorPage";

function MyPage({ setBirth }) {
  const { user, googleUser, authLoading } = useContext(UserContext);
  const { error, loading, request, setError } = useApi();
  const navigate = useNavigate();

  const [updatedUser, setUpdatedUser] = useState({
    name: "",
    newPassword: "",
    birth: "",
  });

  // 1.   
  const loadUser = async () => {
    setError(null);
    try {
      const res = await request(() => api.get("/api/auth/me"));
      setUpdatedUser({
        name: res.name ?? "",
        birth: res.birth ?? "",
        newPassword: "",
      });
    } catch (err) {
      console.error(err);
      if (err.response?.status === 401) {
        alert(" .");
        navigate("/login");
      } else if (err.response?.status === 404) {
        setError("   .   ");
      } else setError("   ");
    }
  };

  useEffect(() => {
    if (authLoading) return;

    if (user) {
      loadUser();
    } else {
      navigate("/err401", { replace: true });
      return;
    }
  }, [user, authLoading]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUpdatedUser((prev) => ({ ...prev, [name]: value }));
  };

  // 2.     . payload  value (updatedUser)  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);

    const payload = {
      ...(updatedUser.name && { name: updatedUser.name }),
      ...(updatedUser.birth && { birth: updatedUser.birth }),
      ...(updatedUser.newPassword && { password: updatedUser.newPassword }),
    };

    // **  **           alert  
    // if (payload.name === updatedUser.name && payload.birth === updatedUser.birth) {
    //   alert("  .");
    //   loadUser();
    //   return;
    // }

    try {
      await request(() => api.patch("/api/auth/profile", payload));
      alert(" .");
      //   ,  
      setUpdatedUser((prev) => ({
        ...prev,
        name: payload.name ?? prev.name,
        birth: payload.birth ?? prev.birth,
        newPassword: "",
      }));
      setBirth(payload.birth);
    } catch (err) {
      if (err.response?.status === 400) {
        //    
        setError(err.response.data);
      } else {
        setError(" ");
        console.error(err);
      }
    }
  };

  if (loading) return <div> ...</div>;
  if (error && typeof error === "string" && error.includes("")) {
    //     ""   (   ..)
    return <ErrorPage statusCode={500} message={error} />;
  }

  return (
    <div className="mypage-container">
      <div className="mypage-header">
        <h2>My Page</h2>
      </div>

      {/* error    */}
      {error && typeof error === "object" ? (
        <>
          {error.name && <p className="error-message">{error.name}</p>}
          {error.birth && <p className="error-message">{error.birth}</p>}
          {error.password && <p className="error-message">{error.password}</p>}
        </>
      ) : (
        error && <p className="error-message">{error}</p>
      )}

      <form onSubmit={handleSubmit}>
        <div className="mypage-card">
          <div className="mypage-grid">
            <div className="label">  </div>
            {googleUser ? (
              <input
                type="text"
                name="text"
                value={googleUser}
                className="ro-input"
                readOnly
              />
            ) : (
              <input
                type="text"
                name="text"
                value="        ."
                className="ro-input"
                readOnly
              />
            )}

            <div className="label"></div>
            <input
              type="text"
              name="username"
              value={user}
              className="ro-input"
              readOnly
            />

            <div className="label"> </div>
            <input
              type="password"
              name="newPassword"
              value={updatedUser.newPassword ?? ""}
              onChange={handleChange}
              autoComplete="new-password"
            />

            <div className="label"></div>
            <input
              type="text"
              name="name"
              value={updatedUser.name}
              onChange={handleChange}
              required
            />

            <div className="label"></div>
            <input
              type="text"
              name="birth"
              value={updatedUser.birth}
              onChange={handleChange}
              required
            />
          </div>
        </div>

        <button className="submit-btn" type="submit">
           
        </button>
      </form>
    </div>
  );
}

export default MyPage;
