import { useContext, useEffect, useState } from "react";
import api from "../api/axiosConfig";
import { useNavigate } from "react-router-dom";
import "../css/ContactAdmin.css";
import { UserContext } from "../context/UserContext";
import useApi from "../hooks/useApi";

function ContactAdmin() {
  // UserRole  ADMIN      
  const [rooms, setRooms] = useState([]);
  // const [loading, setLoading] = useState(true);
  const { loading, request } = useApi();
  const navigate = useNavigate();
  const { user, userRole } = useContext(UserContext);

  useEffect(() => {
    if (user === null) return;
    if (userRole !== "ADMIN") {
      alert("  .");
      navigate("/");
      return;
    }
    loadChatRooms();
  }, [user, userRole]);

  // USER      
  const loadChatRooms = async () => {
    try {
      const res = await api.get("/api/admin/chat");
      console.log("", res.data);
      setRooms(res.data);
    } catch (err) {
      console.error("  :", err);
    }
  };

  //     -        .
  const deleteRoom = async (roomId) => {
    if (!window.confirm("  ?")) return;
    try {
      await api.delete(`/api/admin/chat/${roomId}`, {
        withCredentials: true,
      });
      await loadChatRooms();
    } catch (err) {
      console.error("   : ", err);
    } finally {
      navigate("/contact_admin");
    }
  };

  if (loading) return <div className="chatlist-wrap"> ...</div>;

  return (
    <div className="chatlist-wrap">
      <h2 className="chatlist-title">  </h2>
      <div className="chatlist-container">
        {rooms.length === 0 && <div className="chatlist-empty">   .</div>}

        {rooms.map((room) => (
          <div
            key={room.id}
            className="chatlist-item"
            onClick={() => navigate(`/contact/${room.id}`)} //   roomId 
          >
            <div className="chatlist-user">{room.username}</div>
            <div className="chatlist-msg">{room.lastMessage || " "}</div>
            <button
              className="chat-delete"
              onClick={(e) => {
                e.stopPropagation(); //      **********
                deleteRoom(room.id);
              }}
            >
                
            </button>
            <div className="chatlist-date">
              {room.updatedAt
                ? room.updatedAt.slice(0, 16).replace("T", " ")
                : room.createdAt?.slice(0, 16).replace("T", " ")}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default ContactAdmin;
