import { useState } from "react";

//  ( , )
const useApi = () => {
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const request = async (apiCall) => {
    setError(null);
    setLoading(true);
    try {
      const response = await apiCall();
      setLoading(false);
      return response.data; //  res  data   return 
    } catch (err) {
      setLoading(false); //    false 
      if (err.response) {
        //    (status)  (message) 
        //    :  (Optional Chaining) 
        // err.response undefined  null  undefined    
        setError(err.response.data?.message || ` : ${err.response.status}`);
      } else {
        setError("  .");
      }
      throw err; //      
    }
  };

  return { error, loading, request, setError };
};

export default useApi;
